<template>
  <div class="componentRow">
    <div class="imgBack" ref="imgFrame">
        <img v-bind:src="componentData.imgUrl" class="titleImg">
    </div>
    <span class="componentTitle">{{componentData.title}}</span>
    <div class="rowAfter" @click="rowClick()">
        <span class="componentDesc">{{componentData.desc}}</span>
        <img src="static/arror.png" class="arrorImg">
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'hospitalInfoList',
  props: {
    componentData: {
      type: Object,
      default: {}
    }
  },
  mounted () {
    this.$refs.imgFrame.style['background-color'] = this.componentData.color
  },
  data () {
    return {
    }
  },
  methods: {
    rowClick: function () {
      this.$emit('infoListClick', this.componentData.title)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$height_row = 60px
$height_imgBack = 34px
$height_img = 22px

.componentRow
    height : $height_row
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    border-top: 1px solid $color-theme-gray;

.imgBack
    height : $height_imgBack
    width : $height_imgBack
    @extend .flex-around
    border-radius : 10px
    margin-left : 20px

.titleImg
    height : $height_img
    margin-left : -2px

.arrorImg
    height : $height_img
    margin-right : 10px

.componentTitle
    color : $color-theme-gray_2
    font-weight : $font-weight-level_4
    font-size : $font-size-level_4
    margin-left : 10px

.componentDesc
    font-size : $font-size-level_0
    color : $color-theme-gray_1
    margin-right : 15px
    font-weight : $font-weight-level_5

.rowAfter
    @extend .flex
    @extend .flex-row
    @extend .flex-end
    flex-grow : 2

</style>



