<template>
    <transition name="slide">
        <div>
            <img src="static/hospitalBanner.jpeg" class="headImg">
            <div class="hospitalTitleRow">
                <img src="static/hospitalLogo.jpeg" class="hospitalLogoImg">
                <span class="hospitalTitle">上海东方医院</span>
                <span class="hospitalLevle">三级甲等</span>
            </div>
            <hospital-info-list v-for="(item, index) in lists" :key="index" :componentData="item" v-on:infoListClick="infoListClick"></hospital-info-list>
            <div class="dowmBox" ref="dowmBox">
                <div class="boxContain" ref="boxContain">
                    <div class="route">
                        <div class="routeHead">
                            <div class="frameTag"></div>
                            <span>交通方式</span>
                        </div>
                        <p>乘车路线：67/91/20路公交车</p>
                    </div>
                    <div class="route">
                        <div class="routeHead">
                            <div class="frameTag"></div>
                            <span>医院简介</span>
                        </div>
                        <pre>上海市东方医院（同济大学附属东方医院）始建于1920年，地处浦东陆家嘴金融区，是一所集医疗、教学、科研、急救、预防、康复、保健于一体的大型三级甲等综合性医院，2015年被中央文明委授予“全国文明单位”。医院南北两址，设61个临床、医技科室，开放床位2000张，年门急诊人次超过300万，其中外籍患者逾5万人次。拥有炫速双源CT、PET-CT、3.0T核磁共振、DSA、EDGE放射手术系统、杂交手术室、辅助循环系统等国际先进的大型医疗设备。</pre>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script type="text/ecmascript-6">

import hospitalInfoList from 'components/hospitalInfoList/hospitalInfoList'

export default {
  name: 'hospitalInfo',
  data () {
    return {
      lists: [
        {
          title: '医院官网',
          imgUrl: 'static/ie.png',
          desc: 'http://www.easthospital.cn',
          color: '#7fcef5'
        },
        {
          title: '医院电话',
          imgUrl: 'static/mobile.png',
          desc: '021-58356602',
          color: '#5cce8b'
        },
        {
          title: '医院地址',
          imgUrl: 'static/location.png',
          desc: '上海市浦东新区即墨路150号',
          color: '#feb12b'
        }
      ]
    }
  },
  methods: {
    infoListClick: function (info) {
      console.log(info)
    }
  },
  mounted () {
    this.$refs.dowmBox.style.height = parseInt(window.getComputedStyle(this.$refs.boxContain).height) + 50 + 'px'
  },
  components: {
    hospitalInfoList
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$height_row_title = 50px

.headImg
    width 100%

.hospitalTitleRow
    height $height_row_title
    @extend .flex
    @extend .flex-row
    @extend .flex-start

.hospitalLogoImg
    width 34px
    margin-left 20px

.hospitalTitle
    color rgb(204, 51, 0)
    margin-left 10px
    font-weight $font-weight-level_5

.hospitalLevle
    font-size $font-size-level_0
    color $color-theme-gray_1
    margin-left 10px
    font-weight $font-weight-level_5

.frameTag
    width 4px
    height 20px
    background-color $color-theme-blue

.route
    margin 20px 10px 0px 10px
    background-color white
    p
        text-align left
        margin 10px 20px 0px 20px
        font-size $font-size-level_2
        padding-bottom: 10px
    pre
        white-space pre-wrap
        word-wrap break-word
        text-align left
        margin 10px 20px 0px 20px
        font-size $font-size-level_2
        padding-bottom 10px
        line-height 19px

.routeHead
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    padding-top 10px
    padding-left 2px
    span
        margin-left 10px
        color $color-theme-blue
        font-weight $font-weight-level_5
        font-size $font-size-level_4

.dowmBox
    background-color $color-theme-gray_3
    position absolute

.boxContain
    position relative
    top 10px
</style>



